.production-chart-container {
    width: 100vw;
    height: 100vh;
    
    opacity: 0; /* Start with opacity 0 */
    animation: fadeIn 3s forwards cubic-bezier(0.5, 1, 0.89, 1); /* Apply fade-in animation */
}

@keyframes fadeIn {
    from {
        opacity: 0; /* Start opacity */
    }
    to {
        opacity: 1; /* End opacity */
    }
}
  
.production-chart {
    width: 100vw;
    height: 90vh;
}

.production-graph {
    width: 90vw !important;
    height: 90vh !important;
    margin: auto;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
}

.text-color, .p-text {
    color: azure;
}

.date-range {
    display: flex;
    justify-content: flex-start;
    height: 10px;
    margin-left: 5%;
    font-family: "Gill Sans Extrabold", sans-serif;
}